import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import fecth from 'isomorphic-fetch';
import Websocket from 'react-websocket';
//import $ from 'jquery';
import classNames from 'classnames';
import Item from './components/item.js';
import logo from './images/logo.png';
import './index.css';
import './App.css';
// import bulma css
import 'bulma/css/bulma.css'
// import react-highcharts
import ReactHighcharts from 'react-highcharts';

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      mainData : {},
      transHistory : [
        {
          "82045":[
            1495236863,
            "291 byte",
            534470119.0000001,
            1,
            "26b8f77a027f83f72563ea5dc521be76ca3f985115419b4ca715b0e09a95caef"
          ]
        }
      ],
      nightMode : false,
    }
  }
  componentWillMount() {
    fetch("stats.json")
    .then(response => response.json())
    .then(json => {
      this.setState({ mainData : json});
    })
    .catch(error => console.log(error));

    this._chartConfig = {
      chart: {
        height: 190,
        backgroundColor: '#FFFFFF'
      },
      credits: {
          enabled: 0
      },
      title: {
        text: null
      },
      series: [{
          type: 'column',
          colorByPoint: true,
          data: [29.9, 71.5, 106.4, 129.2, 144.0, 176.0, 135.6, 148.5, 216.4, 194.1, 95.6, 54.4],
          showInLegend: false,
          pointPadding: 0,
          groupPadding: 0
      }],
      yAxis: {
        visible: false
      },
      xAxis: {
        visible: false
      },
      plotOptions: {
        borderWidth : 0
      },
      column: {
        groupPadding : 0
      }
    };

    //this.getdata('currentSuply');
    //this.getdata('difficulty');
    //this.getdata('hashrate');
    //his.getdata('transHistory');
  }
  componentDidMount()
  {

  }
  getdata(item) {
    let query = '';

    switch(item) {
      case 'currentSuply':
        query = 'totalcoins';
        break;
      case 'difficulty':
        query = 'getdifficulty';
        break;
      case 'hashrate':
        query = 'hashrate';
        break;
      case 'transHistory':
        query = 'lasttxs';
        break;
      default:
        break;

    }
    fecth('http://chainz.cryptoid.info/vrm/api.dws?key=0cc8d595b649&q=' + query, {mode: 'cors'})
    .then((response) => {
        console.log(response);
        return response.json()
    })
    .then((recurso) => {
        console.log(recurso);
        this.setState({ [item] : recurso});
    })
    .catch((error) => {
      console.log(error);
    });
  }
  handleData = (field) => (data) => {
    let result = JSON.parse(data);
    if(field === 'main') {
      this.setState({ mainData : result});
    }
    if(field === 'blocks') {
      console.log(data);
      let history = this.state.transHistory;
      history.push(result.newblock)
      this.setState({ transHistory : history});
    }
  };
  _handleNightMode = (e) => {
    e.preventDefault();

    this.setState({nightMode : !this.state.nightMode});

    let nightModeChartConfig = this._chartConfig;
    nightModeChartConfig.chart.backgroundColor = !this.state.nightMode ? '#111111' : '#FFFFFF';
    this._chartConfig = nightModeChartConfig;
  };
  renderHistory() {
    let rows = [];
    if(this.state.transHistory.length > 0) {
      rows = this.state.transHistory.map((value, index) => {
        let mk = Object.keys(value)[0];
        return (
          <tr key={index}>
            <td>{mk}</td>
            <td>{value[mk][0]}</td>
            <td>{value[mk][1]}</td>
            <td>{value[mk][3]}</td>
            <td>{value[mk][2]}</td>
          </tr>
        );
      });
    }else{
      rows = <tr><td colSpan={5} className="text-center">fetching data...</td></tr>;
    }

    return rows;
  }
  render() {
    let containerClass = classNames({
        'nightMode' : this.state.nightMode
    });
    document.body.className = containerClass;
    return (
      <div className='container'>
        <Websocket url='ws://explorer.vrm.vericoin.info/poll/VRM_blocks' onMessage={this.handleData('blocks')}/>
        <Websocket url='ws://localhost:8080' onMessage={this.handleData('main')}/>
        <div className="columns">
          <div className="column is-one-quarter">
            <div style={{textAlign: 'center', padding: 10}}>
              <img src={logo} alt="Verium" style={{width : 130, paddingBottom : 20}}/>
              <br/>
              <h2 className="title">MINING STATISTICS</h2>
            </div>
            <Item icon={'check-circle-o'} title="Current Supply">
              {parseInt(this.state.mainData.totalsupply)} VRM
            </Item>
            <Item icon={'globe'} title="Network Hash">
              {toFixed(parseFloat(this.state.mainData.nethashrate), 2)} GH/s
            </Item>
            <Item icon={'star'} title="Mining Reward">
              {toFixed(parseFloat(this.state.mainData.blockreward), 2)} VRM
            </Item>
            <Item icon={'cogs'} title="Blocks">
              {this.state.mainData.blocks}
            </Item>
            <Item icon={'clock-o'} title="Block Time">
              {toFixed(parseFloat(this.state.mainData.blocktime), 2)}
            </Item>
            <Item icon={'line-chart'} title="Network Difficulty">
              {toFixed(parseFloat(this.state.mainData.difficulty), 6)} MH
            </Item>
          </div>
          <div className="column">
            <button className="button" style={{position: 'absolute', top: 30, right: 0, zIndex: 999}} onClick={this._handleNightMode}>Night Mode: {this.state.nightMode ? 'ON' : 'OFF'}</button>
            <div style={{textAlign : 'center', paddingTop: 30}}>
              <div id="map" style={{height: 400, zIndex: 0}}></div>
            </div>
            <div className="columns">
              <div className="column is-half">
                <h3>Blocktime</h3>
                <div className="box">
                  <article className="media">
                    <div className="media-content">
                      <div className="content">
                        <ReactHighcharts config = {this._chartConfig}></ReactHighcharts>
                      </div>
                    </div>
                  </article>
                </div>
              </div>
              <div className="column is-half">
                <h3>Numbers of transactions</h3>
                <div className="box">
                  <article className="media">
                    <div className="media-content">
                      <div className="content">
                        <ReactHighcharts config = {this._chartConfig}></ReactHighcharts>
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="columns">
          <div className="column">
            <h3>Transaction history</h3>
            <table className="table">
              <thead>
                <tr>
                  <th>Height</th>
                  <th>Age</th>
                  <th>Size</th>
                  <th>Transactions</th>
                  <th>Sent</th>
                </tr>
              </thead>
              <tbody>
                {this.renderHistory()}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}
function toFixed(num, fixed) {
    if(num > 0) {
      var re = new RegExp('^-?\\d+(?:\.\\d{0,' + (fixed || -1) + '})?');
      return num.toString().match(re)[0];
    }else{
      return num;
    }
}

ReactDOM.render(
  <App />,
  document.getElementById('root')
);



